.page-header__menu--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 4px;
  top: 0;
  right: 0;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff; }

