  left: 37px;
  width: 5px;
  height: 29px;
  background-color: #ffffff;
  transform: rotate(-45deg); }

.page-header__menu--opened .page-header__toggle::after {
  position: absolute;
  content: "";
  top: -2px;
  left: 37px;
  width: 5px;
  height: 29px;
